import React from 'react'
import phone from '../media/phone.png'
import email from '../media/email.png'

export const Contact = () => {

  return (
    <div className="contact-container">
        <div className="title">Contact:</div>

        <div className="contact-container-inner">
            <div className="contact-left">
                <img src={phone} />
                <div className="phoneInfos">
                    <div className="phoneInfo1">
                        <Phone phone="0 800 801 802" text="apel din România" />
                    </div>
                    <div className="phoneInfo2">
                        <Phone phone="+4021 303 69 69" text="apel din străinătate" />
                    </div>
                </div>
            </div>
            <div className="contact-right">
                <img src={email} />
                <div className="emailInfo">
                    <div className="email">E-mail</div>
                    <div className="txt">Contactați-ne pentru orice informație</div>
                </div>
            </div>
        </div>

    </div>
  )
}

const Phone = ({ phone, text }) => (
    <div className="phoneInfo">
        <div className="phone">{phone}</div>
        <div className="txt">{text}</div>
    </div>
)
