import React, { useState, useEffect } from 'react'
import first from '../media/1.png'
import second from '../media/2.png'
import third from '../media/3.png'
import { CarouselProvider, Slider, Slide, ButtonBack, ButtonNext, Dot, Image } from 'pure-react-carousel';

const items =  [{
  first: 'text_1',
  styleSize: '-658px',
},
{
  first: 'text_2',
  styleSize: '658px'
},
{
  first: 'text_3',
  styleSize: '1316px'
}]

export const Carousel = props => {

  const [counter, setCounter] = useState(0)
  const [size, setSize] = useState(items[0].styleSize)
  const [touched, setTouched] = useState(false)

  const getSize = () => {
    switch(counter) {
      case 0:
        return {
            first: 'text_1',
            styleSize: '0px',
            index: 0,
            active: counter === 0 ? { borderBottom: '4px solid #000', borderTop: 'none'} : { borderBottom : 'none', borderTop: '4px solid #7A96CC;'}

        }
      case 1:
          return {
              first: 'text_2',
              styleSize: '-658px',
              index: 1, 
              active: counter === 1 ? { borderBottom: '4px solid #000', borderTop: 'none'} : { borderBottom : 'none', borderTop: '4px solid #7A96CC;'}

          } 
      case 2:
        return {
          first: 'text_3',
          styleSize: '-1316px',
          index: 2,
          active: counter === 2 ? { borderBottom: '4px solid #000', borderTop: 'none'} : { borderBottom : 'none', borderTop: '4px solid #7A96CC;'}

            }  
      default:
        return {
          first: 'text_1',
          styleSize: '0px',
          index: 0,
          active: counter === 0 ? { borderBottom: '4px solid #000', borderTop: 'none'} : { borderBottom : 'none', borderTop: '4px solid #7A96CC;'}

        }      
    }
  }
 
  useEffect(() => {
    console.log('use effect')
    if (touched) {
      console.log('touched')
      return
    }
    if (counter === 3) {
      setCounter(0)
      return
    }
    setTimeout(() => {
      if (!touched) {
        setCounter(counter + 1)
      }
    }, 4000)
    
  }, [counter])
//style={{backgroundImage: counter % 2 === 0 ? `url(${first}` : `url(${second})`}}
  const linkItems = [
    {
      id: 0,
      text: 'Un nou inceput!',
      left: '0px',
    },
    {
      id: 1,
      text: 'Ghiseul.ro!',
      left: '-658px',

    },
    {
      id: 2,
      text: 'Tentativa de frauda!',
      left: '-1316px',

    }
  ]

  const linkStyle = (id) => {
    return id === counter ? { borderBottom: '4px solid #000', borderTop: 'none', borderLeft: 'none', paddingTop: '9px'} : { borderBottom : 'none', borderTop: '4px solid #7A96CC', borderLeft: '1px solid #BDD6FD', paddingTop: '5px'}

  }
  const handleChange = (id, left) => {
    setTouched(true)
    setCounter(id)
    setSize(left)
  }

  return (
    <div className="carousel-container" style={{transition: `ease-in-out ${counter >=2 ? 0.5 : 2}s`}}>
      <div className="carousel-item-wrapper">
        <div className="carousel-wrapper" style={{left: !touched ? getSize().styleSize : size}}>
          <img src={first} className="first"/>
          <img src={third} />
          <img src={second} className="second"/>

        </div>
      </div>
      <div className="carousel-menu">
        {linkItems.map((item, index) => {
          return(
          <div className="carousel-item-link" style={linkStyle(item.id)} onClick={() => handleChange(item.id, item.left)}><span>{item.text}</span></div>
          )
        })}
       

      </div>

    </div>
    
  )
}