import React, { useState, useEffect } from 'react'

import { storage } from '../storage/storage'
import { Step_1 } from './step_1'
import { STEPS } from '../utils/constants'
export const Steps = props => {

  const { store: { step } } = props

  const getStep = (step) => {
    switch(step) {
      case STEPS.STEP_1:
        return <Step_1 {...props} />
      default:
        return null
    }
  }
  return(
    <React.Fragment>
      {getStep(step)}
    </React.Fragment>
  )
}


export const DynamicContainer = props => {

  return (
    <React.Fragment>
      <Steps {...props} />
    </React.Fragment>
  )
}