import React from 'react'
import list_1 from '../media/list_1.png'
import list_2 from '../media/list_2.png'
import list_3 from '../media/list_3.png'

const articles = [
    {
        img: list_1,
        title: 'Noua aplicație de internet și Mobile Banking de la First Bank!',
        desc: 'Când vine vorba de internet banking, meriti mai mult!'
    },
    {
        img: list_2,
        title: 'Intră acum și în câteva minute ai scăpat de grija taxelor!',
        desc: 'Dai grija taxelor pe timp liber, cu winbank!'
    },
    {
        img: list_3,
        title: 'Tentativă de fraudă!',
        desc: 'ATENŢIE: Încercare de furt de date (phishing)'
    },

]

export const Article = ({ article: { img, title, desc } }) => {

  return (
    <div className="article-item">
      <div className="article-img">
        <img src={img} />
      </div>
      <div className="article-titles">
        <div className="article-title">{title}</div>
        <div className="article-description">{desc}</div>
      </div>
    </div>
  )
}

export const Articles = props => {

  return (
    <div className="articles-container">
        <div className="head">Ştiri</div>
        { articles.map(article => <Article article={article}/> )}
    </div>
  )
}