import React from 'react'
import security from '../media/security.png'

export const Security = () => {

  return (
    <div className="security-container">
        <div className="security-img">
            <img src={security} />
            <div className="security-img-title">Securitate</div>
            <div className="security-img-txt">
                winbank recunoaşte importanţa securităţii tranzacţiilor dumneavoastră şi a încorporat cele mai moderne tehnici şi metode de protecţie.
                <br/><br/>
                <a href="#">Citeste mai multe</a>
            </div>
        </div>
    </div>
  )
}
